.instruction {
    min-height: 824px;
    padding: 96px 0;
    &__wrapper {
        width: 1280px;
        margin: 0 auto;
        padding: 0 32px;
    }
    &__question {
        color: var(--opacity-color);
        text-align: center;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px; /* 150% */
    }
    &__title {
        margin-top: 12px;
        text-align: center;
    }
    &__descr {
        width: 768px;
        margin: 20px auto 0 auto;
        color: var(--opacity-color7);
        text-align: center;
        font-size: 20px;
        font-weight: 400;
        line-height: 30px; /* 150% */
    }
    &__box {
        margin-top: 64px;
        display: grid;
        grid-template: repeat(2, minmax(172px, auto)) / repeat(3, 1fr);
        grid-auto-rows: minmax(172px, auto);
        gap: 64px 32px;
    }
    &__round {
        width: 48px;
        height: 48px;
        margin: 0 auto;
        border-radius: 100%;
        background-color: var(--cream-color);
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__icon {
        max-width: 22px;
        max-height: 22px;
        object-fit: cover;
    }
    &__item-title {
        margin-top: 20px;
        text-align: center;
    }
    &__text {
        margin-bottom: 0;
        color: #667085;
        text-align: center;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px; /* 150% */
    }
}