.articles {
    padding: 97px 0;
    &__title {
        width: 680px;
        margin: 0 auto;
        text-align: center;
    }
    &__wrapper {
        margin: 65px 136px 0 154px;
        display: flex;
        justify-content: space-between;
    }
    &__item {
       width: 356px;
       height: 380px;
       overflow: hidden;
       border: 1px solid #EDEFF5;
       background: var(--main-color);
    }
    &__img {
        width: 100%;
        height: 160px;
        object-fit: cover;
    }
    &__text {
        padding: 24px 28px;
    }
    &__name {
        text-decoration: none;
        color: var(--title-color);
        font-size: 24px;
        font-weight: 700;
        line-height: 32px; /* 133.333% */
        letter-spacing: -0.72px;
        &:hover {
            color: var(--title-color);
        }
    }
    &__descr {
        margin: 12px 0 0 0;
        color: var(--grey-color);
        font-size: 16px;
        font-weight: 400;
        line-height: 24px; /* 150% */
        letter-spacing: -0.32px;
    }
    &__btn {
        margin: 55px auto 0 auto;
    }
}