.companies {
    min-height: 296px;
    padding: 96px 0;
    &__wrapper {
        width: 1280px;
        margin: 0 auto;
        padding: 0 32px;
    }
    &__descr {
        color: #667085;
        text-align: center;
        font-size: 16px;
        font-weight: 500;
        line-height: 24px; /* 150% */
    }
    &__box {
        margin-top: 32px;
        display: flex;
        justify-content: space-between;
    }
    &__logo {
        max-height: 48px;
        max-width: 187px;
    }
}