.btn {
    display: block;
    height: 42px;
    width: 250px;
    border-radius: 10px;
    border: none;
    background-color: var(--comlementary-color);
    text-align: center;
    color: var(--main-color);
    font-size: 14px;
    line-height: 42px;
    font-weight: 400;
    transition: 0.6s all;
        &:hover {
            box-shadow: 0px 0px 10px rgba(0,0,0,0.5);
            color: black;
            background-color: var(--main-color);
        }
    &_menu {
        height: 100%;
        width: 193px;
        border-radius: 4px;
        line-height: 40px;
        font-weight: 600;
    }
    &_form {
        width: 150px;
        height: 44px;
        font-size: 16px;
        font-weight: 600;
        line-height: 24px;
    }
    &_information {
        margin: 32px auto 0 auto;
        height: 44px;
        border-radius: 8px;
        line-height: 44px;
    }
}