.title {
    color: var(--title-color);
    font-size: 48px;
    font-weight: 800;
    line-height: 56px;
    letter-spacing: -0.48px;
    margin-bottom: 0;
    &_fz72 {
        font-size: 72px;
        line-height: 86.4px;
        letter-spacing: -0.72px;
    }
    &_fz40 {
        font-size: 40px;
        line-height: 48px;
    }
    &_fz36 {
        font-size: 36px;
    }
    &_fz30 {
        font-size: 30px;
        line-height: 38px;
    }
    &_fz24 {
        font-size: 24px;
        line-height: 32px;
    }
    &_fz20 {
        font-size: 20px;
        line-height: 30px;
    }
    &_fz20-lh24 {
        font-size: 20px;
        line-height: 24px;
    }
}