.promo {
    margin-top: 60px;
    min-height: 677px;
    background-color: var(--cream-color);
    padding-top: 74px;
    position: relative;
    &_services {
        padding-top: 96px;
        min-height: 787px;
    }
    &__icon {
        display: block;
        position: absolute;
        top: -28px;
        left: 34px;
        max-height: 120px;
        max-width: 120px;
    }
    &__wrapper {
        padding: 0 166px 0 85px;
        display: flex;
        justify-content: space-between;
    }
    &__info {
        margin-top: 66px;
        width: 545px;
        &_services {
            margin-top: 80px;
        }
        &_about-us {
            margin-top: 113px;
        }
    }
    &__descr {
        color: var(--grey-color);
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: -0.32px;
        opacity: 0.4;
    }
    &__title {
        margin-top: 6px;
    }
    &__text {
        margin: 18px 0 0 0;
        color: var(--grey-color);
        font-size: 18px;
        font-weight: 400;
        line-height: 160%;
    }
    &__btn {
        margin-top: 18px;
    }
    &__illustr {
        position: relative;
        width: 462px;
        height: 603px;
    }
    &__img {
        position: block;
        position: relative;
        top: 26px;
        width: 100%;
        height: 100%;
        object-fit: cover;
        &_services {
            top: 0;
        }
    }
    &__picture-descr {
        width: 237px;
        height: 104px;
        background-color: var(--main-color);
        position: absolute;
        top: -21px;
        left: -116px;
        padding: 24px;
        display: grid;
        grid-template: 32px 24px / 56px 117px;
        column-gap: 16px;
    }
    &__picture-cross {
        grid-row: 1 / 3;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &__picture-title {
        color: var(--brand-color);
        font-size: 24px;
        font-weight: 800;
        line-height: 32px;
        letter-spacing: -0.72px;
    }
    &__picture-sign {
        color: var(--grey-color);
        font-size: 12px;
        line-height: 24px;
        font-weight: 400;
        letter-spacing: -0.12px;
    }
    &__about-us-disc {
        width: 194px;
        height: 188px;
        padding: 32px 40px;
        background-color: var(--main-color);
        position: absolute;
        top: 219px;
        left: 392px;
    }
    &__about-us-img {
        display: block;
        width: 39px;
        height: 39px;
        margin: 0 auto;
    }
    &__about-us-text {
        margin-top: 24px;
        color: var(--grey-color);
        text-align: center;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px; /* 150% */
        letter-spacing: -0.32px;
        span {
            color: var(--gray-900, #041434);
            font-size: 16px;
            font-weight: 400;
            line-height: 24px; /* 150% */
            letter-spacing: -0.32px;
        }
    }
    &__about-us-round {
        width: 80px;
        height: 80px;
        background-color: var(--main-color);
        border-radius: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        left: 98px;
        top: -14px;
    }
}