.help {
    padding: 110px 0 80px 0;
    &_services {
        padding: 104px 0 40px 0;
    }
    &__icon {
        display: block;
        max-height: 120px;
        max-width: 120px;
        margin-left: 150px;
    }
    &__wrapper {
        margin: 16px 0 0 150px;
        display: flex;
    }
    &__title {
        margin: 16px 74px 0 0;
        width: 492px;
        &_services {
            margin: 0 auto;
            text-align: center;
            width: 680px;
        }
    }
    &__info {
        margin-bottom: 0;
        width: 468px;
        color: var(--grey-color);;
        font-size: 17px;
        font-weight: 400;
        line-height: 25px; 
    }
    &__wrapper-areas {
        margin-top: 76px;
        display: grid;
        grid-template-columns: repeat(2, 450px);
        column-gap: 20px;
        justify-content: center;
    }
    &__areas {
        margin: 16px 0 0 150px;;
        display: flex;
        &-item {
            padding: 40px;
            width: 380px;
            &_services {
                padding: 56px 75px;
                width: 450px;
                border: 1px solid #EDEFF5;
                &:not(:last-child) {
                    margin-bottom: 20px;
                }
            }
        }
        &-icon {
            display: block;
            max-width: 72px;
            min-height: 72px;
        }
        &-title {
            margin-top: 32px;
            color: #041434;
            font-size: 24px;
            font-weight: 700;
            line-height: 32px;
            letter-spacing: -0.72px;
        }
        &-text {
            margin: 16px 0 0 0;
            color: var(--grey-color);;
            font-size: 16px;
            font-weight: 400;
            line-height: 24px;
            letter-spacing: -0.32px;
        }
        &_services {
            margin: 0;
            flex-direction: column;
            &:last-child {
                margin-top: 60px;
            }
        }
    }
}