@import 'libs/bootstrap-reboot.min.css';


* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Kumbh Sans', sans-serif;
}

.container {
    width: 1440px;
    margin: 0 auto;
    position: relative;
}


:root {
    --comlementary-color: #20AD96;
    --brand-color: #483D8B;
    --cream-color: #FAF8F6;
    --main-color: #ffffff;
    --grey-color: #4A556C; 
    --title-color: #333461; 
    --opacity-color: rgba(74, 85, 108, 0.50);
    --opacity-color7: rgba(74, 85, 108, 0.70);
}

