.guarantee {
    padding: 31px 0 122px 0;
    min-height: 574px;
    &__img {
        display: block;
        max-width: 312px;
        max-height: 266px;
        margin: 0 auto;
        object-fit: cover;
    }
    &__title {
        text-align: center;
    }
    &__text {
        margin: 24px auto 0 auto;
        width: 540px;
        text-align: center;
        color: var((--opacity-color7));
        text-align: center;
        font-size: 20px;
        font-weight: 400;
        line-height: 30px; /* 150% */
    }
}