.conditional {
    min-height: 860px;
    padding: 49px 0 69px 0;
    background-color: var(--cream-color);
    &__descr {
        color: var(--opacity-color);
        text-align: center;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px; /* 150% */
    }
    &__title {
        text-align: center;
        width: 488px;
        margin: 8px auto 0 auto;
    }
    &__wrapper {
        margin-top: 43px;
        display: flex;
        justify-content: center;
    }
    &__item {
        width: 446px;
        min-height: 540px;
        padding: 58px 57px 106px 57px;
        &_yes {
            background-color: var(--main-color);
        }
    }
    &__subtitle {
        text-align: center;
        color: var(--title-color);
        font-size: 22px;
        font-weight: 700;
        line-height: 120%; /* 26.4px */
        letter-spacing: -0.22px;
        span {
            text-decoration: underline;
            text-transform: uppercase;
        }
    }
    &__list {
        list-style-type: none;
        padding-inline-start: 0;
        margin-top: 74px;
        &-item {
            margin: 0 0 28px 32px;
            color: #667085;
            font-size: 16px;
            font-weight: 400;
            line-height: 24px; /* 150% */
            position: relative;
            &:last-child {
                margin-bottom: 0;
            }
            &::before {
                content: url('../icons/sales/No_arrow.svg');
                display: block;
                width: 20px;
                height: 24px;
                object-fit: cover;
                position: absolute;
                left: -32px;
            }
            &_possitive {
                &::before {
                    content: url('../icons/sales/Yes_arrow.svg');
                }
            }
        }
    }
}