.information {
    padding: 96px 0;
    &__wrapper {
        margin: 0 auto;
        width: 1280px;
        padding: 0 32px;
    }
    &__title {
        text-align: center;
    }
    &__subtitle {
        margin-top: 20px;
        color: var(--grey-color);
        text-align: center;
        font-size: 20px;
        font-weight: 400;
        line-height: 30px; /* 150% */
    }
    &__list {
        margin: 64px auto 0 auto;
        width: 768px;
        &-item {
            overflow: hidden;
            display: grid;
            grid-template: repeat(2, auto) / 1fr 24px;
            gap: 8px 24px;
            &:not(:first-child) {
                padding-top: 24px;
                margin-top: 32px;
                border-top: 1px solid  #E4E7EC;
            }
        }
    }
    &__question {
        grid-row: 1 / 2;
        color: #101828;
        font-size: 18px;
        font-weight: 600;
        line-height: 28px; /* 155.556% */
    }
    &__answer {
        grid-row: 2 / 3;
        margin: 0;
        color: var(--grey-color);
        font-size: 16px;
        font-weight: 400;
        line-height: 24px; /* 150% */
        height: 0;
        opacity: 0;
        transition: 0.5s all;
        &_active {
            height: 100%;
            opacity: 1;
            transition: 0.2s all;
        }
    }
    &__trigger {
        grid-row: 1 / 3;
        width: 24px;
        height: 24px;
        border: 2px solid var(--comlementary-color);
        border-radius: 100%;
        position: relative;
        cursor: pointer;
        &::before {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 9;
            width: 14px;
            height: 2px;
            background-color: var(--comlementary-color);
            border-radius: 4px;
        }
        &::after {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 9;
            width: 2px;
            height: 14px;
            background-color: var(--comlementary-color);
            border-radius: 4px;
            opacity: 1;
            transition: 0.4s all;
        }
        &_active {
            &::after {
                opacity: 0;
            }
        }
    }
    &__ask {
        margin-top: 64px;
        padding: 32px 0;
        background-color: var(--cream-color);
        &-title {
            text-align: center;
        }
        &-subtitle {
            margin-top: 8px;
            color: var(--grey-color);
            text-align: center;
            font-size: 18px;
            font-weight: 400;
            line-height: 28px; /* 155.556% */
        }
    }
}