.shedule {
    padding: 96px 0;
    &__wrapper {
        width: 1280px;
        padding: 0 32px;
        margin: 0 auto;
    }
    &__question {
        color: var(--opacity-color);
        font-size: 16px;
        font-weight: 400;
        line-height: 24px; /* 150% */
    }
    &__title {
        margin-top: 12px;
        width: 460px;
    }
    &__descr {
        margin: 20px 0 0 0;
        width: 460px;
        color:  var(--opacity-color7);
        font-size: 20px;
        font-weight: 400;
        line-height: 30px; /* 150% */
    }
    &__wrapper-list {
        margin-top: 64px;
        display: flex;
        justify-content: space-between;
    }
    &__list {
        width: 560px;
        padding: 32px 0;
    }
    &__item {
        display: grid;
        grid-template: repeat(2, auto) / 48px 1fr;
        gap: 8px 16px;
        &:not(:last-child) {
            margin-bottom: 48px;
        }
        &-round {
            grid-row: 1 / 3;
            width: 100%;
            height: 48px;
            border-radius: 28px;
            border: 8px solid #FAF8F6;
            background: #FAF8F6;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        &-icon {
            max-width: 24px;
            max-height: 24px;
            object-fit: cover;
        }
        &-title {
            padding-top: 8px;
            color: var(--title-color);
            font-size: 19px;
            font-weight: 700;
            line-height: 24px; /* 126.316% */
        }
        &-descr {
            color: #667085;
            font-size: 16px;
            font-weight: 400;
            line-height: 24px; /* 150% */
        }
    }
    &__img {
        width: 560px;
        height: 560px;
        object-fit: cover;
    }
}