.notification {
    padding: 48px 0;
    background-color: var(--cream-color);
    min-height: 180px;
    &__wrapper {
        width: 1280px;
        margin: 0 auto;
        padding: 0 32px;
        display: grid;
        grid-template: 38px 30px / 738px 446px;
        gap: 16px 32px;
    }
    &__title {
        grid-row: 1 / 2;
    }
    &__descr {
        grid-row: 2 / 3;
        color: var(--grey-color);
        font-size: 20px;
        font-weight: 400;
        line-height: 30px; /* 150% */
    }
    &__form {
        grid-row: 1 / 3;
        align-self: center;
        display: flex;
        justify-content: space-between;
        &-input {
            width: 280px;
            padding: 10px 14px;
            border-radius: 8px;
            border: 1px solid #D0D5DD;
            background-color: var(--main-color);
            box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05);
            font-size: 16px;
            font-weight: 400;
            line-height: 24px; /* 150% */
            &::placeholder {
                color: var(--opacity-color);
            }
            &:focus {
                outline: 1px solid var(--comlementary-color);
            }
        }
    }
}