.guide {
    min-height: 878px;
    padding: 158px 0 139px 0;
    &__wrapper {
        margin: 0 175px 0 152px;
        display: flex;
        justify-content: space-between;
    }
    &__info {
        margin-top: 46px;
        width: 550px;
    }
    &__title {
       width: 490px; 
    }
    &__descr {
        margin: 22px 0 0 0;
        color: var(--grey-color);
        font-size: 22px;
        font-weight: 400;
        line-height: 33px; /* 150% */
        span {
            display: block;
            margin-top: 30px;
            font-size: 17px;
            line-height: 20px;
        }
    }
    &__btn {
        margin-top: 22px;
    }
    &__bulletin {
        height: 581px;
        width: 446px;
        background-color: var(--brand-color);
        position: relative;
        &-circle {
            width: 96px;
            height: 96px;
            border-radius: 100%;
            background-color: var(--comlementary-color);
            position: absolute;
            top: -35px;
            right: -32px;
            color: var(--main-color);
            font-size: 14px;
            font-weight: 600;
            text-align: center;
            line-height: 96px;
        }
        &-text {
            width: 218px;
            height: 245px;
            position: absolute;
            top: 38px;
            left: 38px;
            z-index: 99;
            color: var(--main-color);
            font-size: 40px;
            line-height: 62px;
            font-weight: 800;
        }
        &-container {
            width: 341px;
            height: 363px;
            position: absolute;
            top: 218px;
            left: 105px;
            overflow: hidden;
        }
        &-img {
            display: block;
            width: 445px;
            height: 445px;
            object-fit: cover;
        }
        &-opacity {
            height: 79px;
            width: 100%;
            background-color: rgba(65, 58, 125, 0.30);;
            position: absolute;
            bottom: 26px;
            display: grid;
            grid-template: 12px 10px / 30px auto;
            gap: 10px;
            place-content: center center;
        }
        &-chat {
            display: block;
            width: 30px;
            height: 30px;
            border-radius: 100%;
            background-color: var(--main-color);
            text-decoration: none;
            position: relative;
            grid-row: 1 / 3;
            &-img {
                max-width: 100%;
                max-height: 100%;
                object-fit: cover;
                display: block;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
            &-title {
                color: var(--main-color);
                font-size: 12px;
                line-height: 12px;
                font-weight: 600;
            }
            &-descr {
                color: var(--main-color);
                font-size: 10px;
                line-height: 10px;
            }
        }
    }
}