.book {
    padding-bottom: 96px;
    &_sales {
        padding: 165px 0 96px 0;
    }
    &__wrapper {
        width: 1216px;
        min-height: 480px;
        border-radius: 24px;
        margin: 0 auto;
        background-color: var(--cream-color);
        display: flex;
        justify-content: space-between;
    }
    &__info {
        margin-top: 65px;
        padding: 64px 0 64px 63px;
        width: 608px;
    }
    &__descr {
        margin-top: 20px;
        color: var(--opacity-color);
        font-size: 20px;
        font-weight: 400;
        line-height: 30px; /* 150% */
    }
    &__btn {
        margin-top: 20px;
    }
    &__meet {
        margin-top: 48px;
        width: 560px;
        max-height: 512px;
        background: url('../img/online_session.jpg') no-repeat;
    }
    &__link {
        display: block;
        width: 84px;
        height: 32px;
        margin: 20px 0 0 20px;
        padding: 0 18px;
        border-radius: 6px;
        background-color: var(--main-color);
        text-decoration: none;
        display: flex;
        align-items: center;
        justify-content: space-between;
        &::before {
            content: url('../icons/meet.svg');
            width: 17px;
            height: 20px;
        }
        &::after {
            content: 'Meet';
            font-size: 11px;
            font-weight: 600;
            color: #428B7F;
        }
    }
}