.feedback {
    padding: 120px 0 150px 0;
    background-color: var(--cream-color);
    min-height: 879px;
    &_about-us {
        padding-bottom: 199px;
    }
    &__title {
        width: 530px;
        margin-left: 150px;
    }
    &__wrapper {
        margin: 37px 14px 0 150px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    &__item {
        width: 400px;
        min-height: 391px;
        padding: 48px;
        background-color: var(--main-color);
        display: grid;
        grid-template: repeat(4, auto)/ 1fr;
        row-gap: 15px;
        &_about-us {
            min-height: 379px;
        }
    }
    &__mark-about-us {
        color: var(--comlementary-color);
        font-size: 30px;
        font-weight: 800;
        line-height: 40px; /* 133.333% */
        letter-spacing: -0.9px;
    }
    &__text {
        margin-bottom: 0px;
        color: var(--grey-color);
        font-size: 24px;
        font-weight: 400;
        line-height: 32px; /* 133.333% */
        letter-spacing: -0.72px;
        span {
            font-weight: 700;
        }
    }
    &__name {
        color: var(--title-color);
        font-size: 24px;
        font-weight: 700;
        line-height: 32px; /* 133.333% */
        letter-spacing: -0.72px;
    }
    &__photo {
        width: 70px;
        height: 70px;
        border-radius: 100%;
        object-fit: cover;
    }
    &__rating {
        width: 116px;
        display: grid;
        grid-template: 20px / repeat(5, 20px);
        column-gap: 4px;
        &-icon {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}