.footer {
    padding: 64px 0 48px 0;
    &__wrapper {
        width: 1280px;
        margin: 0 auto;
        padding: 0 32px;
        display: grid;
        grid-template-columns: 352px repeat(2, 420px);
        column-gap: 32px;
    }
    &__info {
        padding-right: 32px;
    }
    &__logo {
        max-width: 100%;
        object-fit: cover;
    }
    &__descr {
        margin-top: 32px;
        color: var(--opacity-color);
        font-size: 16px;
        font-weight: 400;
        line-height: 24px; /* 150% */
    }
    &__title {
        color: var(--title-color);
        font-size: 14px;
        font-weight: 800;
        line-height: 20px; /* 142.857% */
    }
    &__list {
        margin-top: 16px;
        list-style-type: none;
        padding-inline-start: 0;
        display: grid;
        grid-template-rows: 24px;
        grid-auto-rows: 24px;
        row-gap: 12px;
        &-link {
            text-decoration: none;
            color: var(--opacity-color);
            font-size: 16px;
            font-weight: 400;
            line-height: 24px; /* 150% */
            transition: 0.6s all;
            &:hover {
                color: var(--comlementary-color);
            }
        }
    }
    &__divider {
        width: 1216px;
        margin: 0 auto;
    }
    &__socials {
        width: 1280px;
        margin: 32px auto 0 auto;
        padding: 0 32px;
        display: flex;
        justify-content: flex-end;
        &-link {
            text-decoration: none;
            margin-left: 24px;
        }
        &-img {
            width: 24px;
            height: 24px;
            object-fit: cover;
        }
    }
}